html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body, h2, p, div {
  margin: 0;
  padding: 0;
}

.navigation {
  margin-right: 50px;
  display: flex;
}

.menu {
  margin: 0;
  padding: 0;
  line-height: 100%;
  list-style: none;
  display: flex;
}

.menu__item {
  text-align: center;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  display: flex;
  position: relative;
}

.menu__item:first-child {
  margin-left: 0;
}

.menu__link {
  color: #060b35;
  padding: 24px 0;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.menu__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  color: #060b35;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  font-style: normal;
  font-weight: 400;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(n+1) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin: auto 50px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__image {
  box-sizing: border-box;
  border: none;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  text-align: start;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  color: #616070;
  justify-content: left;
  align-items: center;
  margin-bottom: 18px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__review {
  justify-content: space-between;
  display: flex;
}

.card__review-title {
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}
