.card {
  width: $card-width;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  padding: 32px 16px 16px;

  display: flex;
  flex-direction: column;
  justify-content: center;

  transition: transform $card-transition-duration;

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
    box-sizing: border-box;
    border: none;
  }

  &__title {
    font-size: $main-font-size;
    font-weight: 500;
    line-height: 18px;
    text-align: start;
    margin-bottom: 4px;
    transition: color $card-transition-duration;
  }

  &__code {
    display: flex;
    align-items: center;
    justify-content: left;
    font-size: 10px;
    line-height: 14px;
    color: $code-font-color;
    margin-bottom: 18px;
  }

  &__review {
    display: flex;
    justify-content: space-between;

    &-title {
      font-size: 10px;
      line-height: 14px;
      margin-bottom: 24px;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    &-title {
      font-size: $main-font-size;
      line-height: 18px;
      color: $price-font-color;
    }

    &-value {
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
      color: $price-value-font-color;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;

    box-sizing: border-box;
    height: 40px;

    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    background-color: $button-background-color;
    border: 1px solid $button-background-color;
    border-radius: 5px;
    color: $button-color;
    text-decoration: none;
    text-transform: uppercase;

    transition: all $card-transition-duration;

    &:hover {
      color: $button-background-color;
      background-color: $button-color;
    }
  }

  &:hover {
    cursor: pointer;
    transform: scale(120%, 120%);
  }

  &:hover &__title {
    color: $card-title-hover;
  }
}
